"use client"

import type React from "react"
import { createContext, useContext, useState, useCallback } from "react"

interface Employee {
  id: string
  name: string
  email: string
  deskId: string
  status: "available" | "occupied" | "out"
  photo?: string // Base64 or data URL
}

interface Desk {
  id: string
  position: { x: number; y: number }
  width: number // add width property
  depth: number // add depth property
  employeeId?: string
  type: "desk"
}

interface KitchenItem {
  id: string
  name: string
  quantity: number
}

interface Room {
  id: string
  type: "kitchen" | "restroom" | "meeting"
  position: { x: number; y: number }
  status: string
}

interface WorkspaceContextType {
  desks: Desk[]
  employees: Employee[]
  kitchenItems: KitchenItem[]
  rooms: Room[]
  selectedDeskId: string | null
  selectedRoomId: string | null
  draggingDeskId: string | null
  draggingRoomId: string | null // add dragging room state
  setSelectedDeskId: (id: string | null) => void
  setSelectedRoomId: (id: string | null) => void
  setDraggingDeskId: (id: string | null) => void
  setDraggingRoomId: (id: string | null) => void // add setter for dragging room
  updateEmployee: (id: string, data: Partial<Employee>) => void
  addEmployee: (employee: Employee) => void
  removeEmployee: (id: string) => void
  updateKitchenItem: (id: string, quantity: number) => void
  addKitchenItem: (item: KitchenItem) => void
  removeKitchenItem: (id: string) => void
  updateRoomStatus: (id: string, status: string) => void
  updateRoomPosition: (id: string, position: { x: number; y: number }) => void // add room position update
  addDesk: (position: { x: number; y: number }) => void
  removeDesk: (id: string) => void
  updateDeskPosition: (id: string, position: { x: number; y: number }) => void
  updateDeskDimensions: (id: string, width: number, depth: number) => void
}

const WorkspaceContext = createContext<WorkspaceContextType | undefined>(undefined)

// Initial mock data
const initialDesks: Desk[] = Array.from({ length: 12 }, (_, i) => ({
  id: `desk-${i + 1}`,
  position: { x: (i % 4) * 3, y: Math.floor(i / 4) * 3 },
  width: 2.5,
  depth: 1.5,
  type: "desk",
}))

const initialEmployees: Employee[] = [
  {
    id: "emp-1",
    name: "Alice Johnson",
    email: "alice@company.com",
    deskId: "desk-1",
    status: "available",
    photo: "data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAUA...",
  },
  { id: "emp-2", name: "Bob Smith", email: "bob@company.com", deskId: "desk-2", status: "occupied" },
  { id: "emp-3", name: "Carol Davis", email: "carol@company.com", deskId: "desk-3", status: "available" },
  { id: "emp-4", name: "David Wilson", email: "david@company.com", deskId: "desk-4", status: "out" },
]

const initialKitchenItems: KitchenItem[] = [
  { id: "kit-1", name: "Coffee", quantity: 10 },
  { id: "kit-2", name: "Tea", quantity: 8 },
  { id: "kit-3", name: "Sugar", quantity: 5 },
  { id: "kit-4", name: "Napkins", quantity: 50 },
  { id: "kit-5", name: "Cleaning Products", quantity: 3 },
]

const initialRooms: Room[] = [
  { id: "room-1", type: "kitchen", position: { x: 12, y: 0 }, status: "ready" },
  { id: "room-2", type: "meeting", position: { x: 12, y: 3 }, status: "available" },
  { id: "room-3", type: "restroom", position: { x: 12, y: 6 }, status: "available" },
]

export function WorkspaceProvider({ children }: { children: React.ReactNode }) {
  const [desks, setDesks] = useState<Desk[]>(initialDesks)
  const [employees, setEmployees] = useState<Employee[]>(initialEmployees)
  const [kitchenItems, setKitchenItems] = useState<KitchenItem[]>(initialKitchenItems)
  const [rooms, setRooms] = useState<Room[]>(initialRooms)
  const [selectedDeskId, setSelectedDeskId] = useState<string | null>(null)
  const [selectedRoomId, setSelectedRoomId] = useState<string | null>(null)
  const [draggingDeskId, setDraggingDeskId] = useState<string | null>(null)
  const [draggingRoomId, setDraggingRoomId] = useState<string | null>(null) // add dragging room state

  const updateEmployee = useCallback((id: string, data: Partial<Employee>) => {
    setEmployees((prev) => prev.map((emp) => (emp.id === id ? { ...emp, ...data } : emp)))
  }, [])

  const addEmployee = useCallback((employee: Employee) => {
    setEmployees((prev) => [...prev, employee])
  }, [])

  const removeEmployee = useCallback((id: string) => {
    setEmployees((prev) => prev.filter((emp) => emp.id !== id))
  }, [])

  const updateKitchenItem = useCallback((id: string, quantity: number) => {
    setKitchenItems((prev) => prev.map((item) => (item.id === id ? { ...item, quantity } : item)))
  }, [])

  const addKitchenItem = useCallback((item: KitchenItem) => {
    setKitchenItems((prev) => [...prev, item])
  }, [])

  const removeKitchenItem = useCallback((id: string) => {
    setKitchenItems((prev) => prev.filter((item) => item.id !== id))
  }, [])

  const updateRoomStatus = useCallback((id: string, status: string) => {
    setRooms((prev) => prev.map((room) => (room.id === id ? { ...room, status } : room)))
  }, [])

  const updateRoomPosition = useCallback((id: string, position: { x: number; y: number }) => {
    setRooms((prev) => prev.map((room) => (room.id === id ? { ...room, position } : room)))
  }, [])

  const addDesk = useCallback((position: { x: number; y: number }) => {
    const newDesk: Desk = {
      id: `desk-${Date.now()}`,
      position,
      width: 2.5, // Default width
      depth: 1.5, // Default depth
      type: "desk",
    }
    setDesks((prev) => [...prev, newDesk])
  }, [])

  const removeDesk = useCallback((id: string) => {
    // Remove associated employee if any
    setEmployees((prev) => prev.filter((emp) => emp.deskId !== id))
    setDesks((prev) => prev.filter((desk) => desk.id !== id))
  }, [])

  const updateDeskPosition = useCallback((id: string, position: { x: number; y: number }) => {
    setDesks((prev) => prev.map((desk) => (desk.id === id ? { ...desk, position } : desk)))
  }, [])

  const updateDeskDimensions = useCallback((id: string, width: number, depth: number) => {
    setDesks((prev) => prev.map((desk) => (desk.id === id ? { ...desk, width, depth } : desk)))
  }, [])

  return (
    <WorkspaceContext.Provider
      value={{
        desks,
        employees,
        kitchenItems,
        rooms,
        selectedDeskId,
        selectedRoomId,
        draggingDeskId,
        draggingRoomId, // add to context
        setSelectedDeskId,
        setSelectedRoomId,
        setDraggingDeskId,
        setDraggingRoomId, // add to context
        updateEmployee,
        addEmployee,
        removeEmployee,
        updateKitchenItem,
        addKitchenItem,
        removeKitchenItem,
        updateRoomStatus,
        updateRoomPosition, // add to context
        addDesk,
        removeDesk,
        updateDeskPosition,
        updateDeskDimensions,
      }}
    >
      {children}
    </WorkspaceContext.Provider>
  )
}

export function useWorkspace() {
  const context = useContext(WorkspaceContext)
  if (!context) {
    throw new Error("useWorkspace must be used within WorkspaceProvider")
  }
  return context
}
